import { switchMap, filter, map, debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { BlogListService } from '../blog-list.service';
import { ScullyRoute } from '@scullyio/ng-lib';
import { fromEvent } from 'rxjs';
import { Router } from '@angular/router';

@Component({
  selector: 'app-blog-list-search',
  templateUrl: './blog-list-search.component.html',
  styleUrls: ['./blog-list-search.component.scss'],
})
export class BlogListSearchComponent implements OnInit {
  @ViewChild('input') inputElement: ElementRef;
  search = new FormControl('');

  constructor(
    private router: Router,
    private blogListService: BlogListService
  ) {}

  ngOnInit(): void {
    // this.blogListService.blogAllList$.subscribe(list => {
    //   console.log('list', list);
    // });

    this.search.valueChanges
      .pipe(
        debounceTime(300),
        distinctUntilChanged(),
        switchMap(v => {
          return this.blogListService.blogAllList$.pipe(
            map(list => {
              console.log('filter', v, list);
              return list.filter((e: ScullyRoute) =>
                e.title.toLowerCase().includes(v.toLowerCase())
              );
            }),
            switchMap(list => {
              return fromEvent(this.inputElement.nativeElement, 'keydown').pipe(
                filter((e: KeyboardEvent) => {
                  return e.key === 'Enter';
                }),
                map(res => {
                  return {list, fq: v};
                })
              );
            })
          );
        })
      )
      .subscribe(res => {
        console.log('re111s', res);
        this.blogListService.blogPageList$.next(res.list);
        this.router.navigate(['blog'], { queryParams: { search: res.fq } });

        // this.router.navigate([{ queryParams: { search: v }]);

      });
  }
}
